import React from 'react';
import './card.scss'
const Card = ({src, title, description}) => {
    return (
        <div className="card-portofolio-molecules">
            <div className="card">
                <img src={src} alt="card-img" className="card-img-top" />
                <div className="card-body">
                    <h5 className="card-title">{title}</h5>
                    <p className="card-text">{description}</p>
                </div>
            </div>
        </div>
    );
};

export default Card;